<template>
  <div v-bind:style="{'height':innerHeight+'px'}" class="hello">
    <div v-for="(item,index) in pages" v-on:click="resetPosition(index)">
      <enter ref="enter" v-bind:title="item.title" v-bind:desc="activeId == index ? item.desc : ''" v-bind:left="item.left" v-bind:top="item.top" v-bind:style="{'transform':'rotate('+item.rotate+'deg)','-ms-transform':'rotate('+item.rotate+'deg)','-moz-transform':'rotate('+item.rotate+'deg)','-webkit-transform':'rotate('+item.rotate+'deg)'}"></enter>
    </div>
    <nav>
      <li v-for="(item,index) in pages"><span v-on:click="resetPosition(index)" v-bind:class="activeId == index ? 'active' : '' "></span></li>
    </nav>
  </div>
</template>

<script>
import Enter from './Enter'

export default {
  name: 'hello',
  data:function(){
    return {
      rangeLeft:{x:[0,0],y:[0,0]},
      rangeRight:{x:[0,0],y:[0,0]},
      rangeTop:{x:[0,0],y:[0,0]},
      rangeCenter:{x:0,y:0},
      innerHeight:'',
    	pages:[{title:'',desc:'',linkto:''}],
      activeId:0
    }
  },
  components : {
    Enter
  },
  mounted:function(){
    var enterW = this.$refs.enter[0].$el.clientWidth;
    var enterH = this.$refs.enter[0].$el.clientHeight;
    var stageW = window.innerWidth;
    var stageH = window.innerHeight;
    this.innerHeight = stageH;
    this.rangeLeft.x[0] = -enterW/2;
    this.rangeLeft.x[1] = (stageW - enterW * 2)/2 - enterW/2;
    this.rangeLeft.y[0] = -enterH/2;
    this.rangeLeft.y[1] = stageH - enterH/2;
    this.rangeRight.x[0] = (stageW - enterW * 2)/2 + enterW * 1.5;
    this.rangeRight.x[1] = stageW - enterW/2;
    this.rangeRight.y[0] = -enterH/2;
    this.rangeRight.y[1] = stageH - enterH/2;
    this.rangeTop.x[0] = (stageW - enterW * 2)/2 - enterW;
    this.rangeTop.x[1] = stageW - (stageW - enterW * 2)/2;
    this.rangeTop.y[0] = -enterH/2;
    this.rangeTop.y[1] = (stageH - enterH*2)/2 - enterH/2;
    this.rangeCenter.x = (stageW - enterW * 2)/2 + enterW/2;
    this.rangeCenter.y = (stageH - enterH*2)/2 + enterH/2;
    var obj = new XMLHttpRequest();
    obj.open('GET','static/test.json',true);
    obj.send(null);
    obj.onreadystatechange = function(){
      if (obj.readyState == 4 && obj.status == 200 || obj.status == 304) { // readyState==4说明请求已完成
        var resultList = JSON.parse(obj.responseText);
        var arr = [];
        resultList.map(function(result){
          arr.push({title:result.title,desc:result.desc,linkto:result.linkto});
        });
        this.pages = arr;
        this.setPosition(0);
      }
    }.bind(this);
  },
  methods:{
    setPosition:function(idd){
      this.pages.map(function(page,index){
        this.$set(page,'rotate',parseInt(Math.random()*60-30,10));
        var m = parseInt(Math.random()*3,10);
        if(m == 0){
          this.setTopPosition(index);
        }else if(m == 1){
          this.setLeftPosition(index);
        }else{
          this.setRightPosition(index);
        }
      }.bind(this));
      this.setCenterPosition(idd);
    },
    setCenterPosition:function(id){
      this.$set(this.pages[id],'left',this.rangeCenter.x);
      this.$set(this.pages[id],'top',this.rangeCenter.y);
      this.pages[id].rotate = 0;
    },
    setLeftPosition:function(id){
      this.$set(this.pages[id],'left',parseInt(Math.random()*(this.rangeLeft.x[1] - this.rangeLeft.x[0]) + this.rangeLeft.x[0]),10);
      this.$set(this.pages[id],'top',parseInt(Math.random()*(this.rangeLeft.y[1] - this.rangeLeft.y[0]) + this.rangeLeft.y[0]),10);
    },
    setRightPosition:function(id){
      this.$set(this.pages[id],'left',parseInt(Math.random()*(this.rangeRight.x[1] - this.rangeRight.x[0]) + this.rangeRight.x[0]),10);
      this.$set(this.pages[id],'top',parseInt(Math.random()*(this.rangeRight.y[1] - this.rangeRight.y[0]) + this.rangeRight.y[0]),10);
    },
    setTopPosition:function(id){
      this.$set(this.pages[id],'left',parseInt(Math.random()*(this.rangeTop.x[1] - this.rangeTop.x[0]) + this.rangeTop.x[0]),10);
      this.$set(this.pages[id],'top',parseInt(Math.random()*(this.rangeTop.y[1] - this.rangeTop.y[0]) + this.rangeTop.y[0]),10);
    },

    resetPosition:function(obj){
      if(this.activeId == obj){
        if(obj == this.pages.length-1){
          window.location.href = 'http://wxlxxx.github.io';
        }else{
          this.$router.push(this.pages[obj].linkto);
        }
      }else{
        this.activeId = obj;
        this.setPosition(obj);
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{overflow: hidden;width: 100%;position: relative;background-color:#f5f5f5;}
nav{position: absolute;bottom: 200px;width: 100%;text-align: center;z-index: 999;}
nav li{display: inline;margin: 0 50px;}
nav li span{background-color: #ccc;padding: 10px;font-size: 0;border-radius: 10px;cursor: pointer;}
nav li span.active{background-color: #608FF7;}
</style>



