<template>
  <div v-bind:style="{'left':left + 'px','top':top + 'px','backgroundImage':'url(static/bg.jpg)'}" class="enter">
	<h2>{{title}}</h2>
	<p>{{desc}}</p>
  </div>
</template>

<script>
export default {
  name: 'enter',
  props : ['title','desc','left','top']
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.enter{position: absolute;width: 290px;height: 170px;background-size: cover;background-position: center;box-sizing: border-box;padding: 20px;transition:left 1s,top 1s;-moz-transition:left 1s,top 1s;-ms-transition:left 1s,top 1s;-webkit-transition:left 1s,top 1s;z-index: 99;cursor: pointer;}
h2{color: #42b983;}
p{color: #999999;}
</style>



